import React from 'react';
import Person from './Person'
import axios from 'axios'
const baseUrl = '/api/persons'

class App extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            persons: [
                {
                    name: '',
                    number: '',
                    id: ''
                }
            ],
            newName: '',
            newNumber: ''
        }
    }


    // is used for updating persons list after adding and deleting users
    updatePersons() {
        axios
            .get(baseUrl)
            .then(response => {
                this.setState({persons: response.data})
            })

    }

    componentDidMount() {
        this.updatePersons()
    }

    addPerson = (event) => {
        //event.preventDefault()
        //console.log('add person pressed')

        //create new objects from user input
        const personObject = {
            name: this.state.newName,
            number: this.state.newNumber
        }

        const person_list = () => this.state.persons.map(part => part.name)
        const number_list = () => this.state.persons.map(part => part.number)
        

        //check if name is already in list
        if (!(person_list().includes(personObject.name)) && (!(number_list().includes(personObject.number)))) {

            //Used before db.json
            /*const persons = this.state.persons.concat(personObject)
            console.log('added: ' + JSON.stringify(personObject))*/

            //update newName and newPerson to empty strings as in beginning
            this.setState({
                //persons: persons, //Used before db.json
                newName: '',
                newNumber: ''
            })

            //post user input into db
            axios
                .post(baseUrl, personObject)
                .then(response =>{

                    if (response.status === 201) {
                        console.log(`Added ${personObject.name} and ${personObject.number}`)

                        this.updatePersons()
                    }
                })

        }
        else{
            alert(` ${personObject.name} is already on list`)
        }
    }

    deletePerson  = (id, person) => {
        return () => {
            const url = (`/api/persons/${id}`)
            //confirm delete from user
            const status = window.confirm(`Do you want to remove user ${person.name} with id ${id}`)

            if (status){
                //delete from db
                axios
                    .delete(url)
                    .then(response => {
                        if (response.status === 200){

                            console.log(`status 200, person ${person.name} deleted`)

                        }
                        //update person list
                        this.updatePersons()
                    })

            }
            else {
                console.log(`canceled  ${person.name} delete `)
            }



        }
    }

    //handle name input
    handleInputChange = (event) => {
        this.setState({ newName: event.target.value })
    }

    //handle number input
    handleNumber = (event) => {
        this.setState({newNumber: event.target.value })
    }


    render() {

        //Used before Person component
        //const person = () => this.state.persons.map(part => <p> {part.name} {part.number} </p>)

        return (
            <div>
                <h2>Phone book</h2>
                <form onSubmit={this.addPerson}>
                    <div>
                        Name:
                        <input
                            onChange={this.handleInputChange}
                            value={this.state.newName}
                        />
                    </div>
                    <div>
                        Number:
                        <input
                            onChange={this.handleNumber}
                            value={this.state.newNumber}
                        />
                    </div>
                    <div>
                        <button type="submit">
                            Add
                        </button>
                    </div>
                </form>
                <h2>Numbers</h2>
                <div>
                    <table>
                        <thead>
                        {this.state.persons.map(part =>
                                <Person
                                    persons={part}
                                    key={part.id}
                                    deletePerson={this.deletePerson(part.id, part)}
                                />)}
                        </thead>
                    </table>
                </div>
            </div>
        )
    }
}

export default App